.transactionHistory {
  width: 450px;
  margin-left: auto;
  margin-right: auto;
  border: 2px black solid;
  border-collapse: collapse;
}

.transactionHistory td,
.transactionHistory th {
  padding-top: 10px;
  border: 1px black solid;
}

.name {
  background-color: rgb(21, 185, 190);
  color: white;
  font-size: 18px;
  font-weight: 900;
}

.name:nth-child(1),
.name:nth-child(2),
.name:nth-child(3) {
  width: 33%;
}

.item {
  text-align: center;
}

.item:nth-child(2n) {
}

.item:nth-child(1),
.item:nth-child(2),
.item:nth-child(3) {
  width: 33%;
}
